"use client";
import React from "react";
import Leftnav from "../../components/Leftnav/leftnav";
import ProfileNav from "@/components/ProfileNav/profilenav";
import MentorProfile from "../../components/MentorProfile/mentorprofile";

const MentorSearch = () => {
  const users = [
    { userImage: "https://via.placeholder.com/40x40", username: "John Doe" },
    { userImage: "https://via.placeholder.com/40x40", username: "Alice Smith" },
    { userImage: "https://via.placeholder.com/40x40", username: "David Lee" },
    { userImage: "https://via.placeholder.com/40x40", username: "Emma Brown" },
    { userImage: "https://via.placeholder.com/40x40", username: "Michael Scott" },
    { userImage: "https://via.placeholder.com/40x40", username: "Sophia Green" },
  ];

  return (
    <div className="flex h-screen overflow-hidden">
      {/* Left Navigation */}
      <div className="hidden md:block md:w-1/5 h-screen">
        <Leftnav />
      </div>

      {/* Main Content */}
     
        <div className="bg-white overflow-y-auto no-scrollbar px-4 flex-col space-y-6 mx-12 ">
          {users.map((user) => (
            <div key={user.username}>
              <MentorProfile username={user.username} userImage={user.userImage} />
            </div>
          ))}
        </div>
      </div>
   
  );
};

export default MentorSearch;
