"use client";
import React, { useState } from "react";
import Ratings from "../Ratings/ratings";

const mentorprofile = ({username,userImage}) => {


  return (
    <>
      <div className="text-black bg-white rounded-lg shadow-md overflow-hidden flex justify-between items-center px-8">
        <div className="flex items-center gap-2 m-3">
          <img
            src={userImage}
            alt="vidhi"
            className="h-12 w-12 rounded-full object-cover"
          />
          <div className="flex flex-col">
            <div className="text-black  font-semibold text-2xl">{username}</div>
            <Ratings />
          </div>
        </div>
        <button className="border p-2 px-4 bg-blue-500 text-white rounded-3xl hover:bg-blue-300">
          Send Request
        </button>
      </div>
    </>
  );
};

export default mentorprofile;
